import { Document, BLOCKS } from '@contentful/rich-text-types';

// Stop words to filter out
const STOP_WORDS = new Set([
  'a', 'an', 'and', 'are', 'as', 'at', 'be', 'by', 'for', 'from', 'has', 'he',
  'in', 'is', 'it', 'its', 'of', 'on', 'that', 'the', 'to', 'was', 'were',
  'will', 'with', 'this', 'but', 'they', 'have', 'had', 'what', 'when', 'where',
  'who', 'which', 'why', 'how', 'can', 'could', 'should', 'would', 'if', 'or'
]);

/**
 * Extract plain text from Contentful Rich Text
 */
export function extractTextFromRichText(document: Document): string {
  let text = '';
  
  const traverse = (node: any) => {
    if (node.nodeType === 'text') {
      text += node.value + ' ';
    }
    if (node.content) {
      node.content.forEach(traverse);
    }
  };

  traverse(document);
  return text.toLowerCase().replace(/[^\w\s]/g, '');
}

/**
 * Calculate Cosine Similarity between two text strings (TF-IDF Lite)
 */
export function calculateSimilarity(textA: string, textB: string): number {
  const tokenize = (text: string) => 
    text.toLowerCase().split(/\s+/).filter(w => w.length >= 2 && !STOP_WORDS.has(w));

  const tokensA = tokenize(textA);
  const tokensB = tokenize(textB);

  const uniqueTokens = new Set([...tokensA, ...tokensB]);
  const vecA = Array.from(uniqueTokens).map(t => tokensA.filter(x => x === t).length);
  const vecB = Array.from(uniqueTokens).map(t => tokensB.filter(x => x === t).length);

  const dotProduct = vecA.reduce((sum, val, i) => sum + val * vecB[i], 0);
  const magA = Math.sqrt(vecA.reduce((sum, val) => sum + val * val, 0));
  const magB = Math.sqrt(vecB.reduce((sum, val) => sum + val * val, 0));

  if (magA === 0 || magB === 0) return 0;
  return dotProduct / (magA * magB);
}

/**
 * Heuristic Oracle: Extract FAQs from content headers
 */
export function extractFAQs(document: Document): { question: string; answer: string }[] {
  const faqs: { question: string; answer: string }[] = [];
  const content = document.content;

  for (let i = 0; i < content.length - 1; i++) {
    const node = content[i];
    
    // Check if node is a heading
    if ([BLOCKS.HEADING_1, BLOCKS.HEADING_2, BLOCKS.HEADING_3, BLOCKS.HEADING_4, BLOCKS.HEADING_5, BLOCKS.HEADING_6].includes(node.nodeType as any)) {
      // Extract heading text
      const question = (node.content[0] as any)?.value?.trim();
      
      if (!question) continue;

      // Heuristic: Does it start with a question word?
      if (/^(What|How|Why|Can|Does|Is|Where|When)/i.test(question)) {
        // Look ahead for the answer (next paragraph)
        const nextNode = content[i + 1];
        if (nextNode.nodeType === BLOCKS.PARAGRAPH) {
          // Extract text from paragraph (simple recursive text extraction)
          let answer = '';
          const traverse = (n: any) => {
            if (n.nodeType === 'text') answer += n.value;
            if (n.content) n.content.forEach(traverse);
          };
          traverse(nextNode);
          
          if (answer.trim().length > 20) { // Valid answer
             faqs.push({ question, answer: answer.trim() });
          }
        }
      }
    }
    
    if (faqs.length >= 3) break; // Limit to 3
  }

  return faqs;
}
