"use client"

import { useEffect, useState } from "react"
import { usePathname } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { calculateSimilarity } from "@/lib/vector-utils"
import { ArrowRight, Search } from "lucide-react"

interface SearchIndexItem {
  title: string;
  slug: string;
  description: string;
}

export function NotFoundContent() {
  const pathname = usePathname()
  const [suggestions, setSuggestions] = useState<SearchIndexItem[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Client-side analytics
    if (typeof window !== 'undefined' && (window as any).gtag) {
      (window as any).gtag('event', '404_error', {
        page_path: pathname
      });
    }

    // Lazarus Protocol: Vector Recovery
    async function recoverLostTraffic() {
      try {
        console.log('ðŸ” Lazarus Protocol: Fetching index...');
        const res = await fetch('/search-index.json');
        if (!res.ok) {
            console.error('âŒ Lazarus: Failed to fetch index', res.status);
            return;
        }
        
        const index: SearchIndexItem[] = await res.json();
        console.log(`âœ… Lazarus: Index loaded (${index.length} items)`);
        
        // Clean the pathname to get a "query"
        // e.g. "/blog/agentic-ai-guide" -> "agentic ai guide"
        const query = pathname
          .replace(/^\/|\/$/g, '') // trim slashes
          .replace(/blog|services|glossary/g, '') // remove section prefixes
          .replace(/[-_/]/g, ' ') // replace separators with spaces
          .trim();

        console.log(`ðŸ” Lazarus Query: "${query}"`);

        if (!query) {
          setLoading(false);
          return;
        }

        const matches = index
          .map(item => ({
            ...item,
            score: calculateSimilarity(query, `${item.title} ${item.description}`)
          }))
          .sort((a, b) => b.score - a.score); // Sort first

        console.log('ðŸ† Top Matches:', matches.slice(0, 3).map(m => `${m.title} (${m.score.toFixed(3)})`));

        const filteredMatches = matches
          .filter(item => item.score > 0.1) // Minimum threshold
          .slice(0, 3);

        setSuggestions(filteredMatches);
      } catch (err) {
        console.error('Lazarus Protocol Failed:', err);
      } finally {
        setLoading(false);
      }
    }

    recoverLostTraffic();
  }, [pathname]);

  return (
    <div className="flex flex-col items-center justify-center min-h-[60vh] text-center max-w-2xl mx-auto px-4">
      <div className="bg-muted/30 p-4 rounded-full mb-6">
        <Search className="w-8 h-8 text-muted-foreground" />
      </div>
      
      <h1 className="text-4xl md:text-5xl font-bold mb-4 tracking-tight">404 - Lost in the Void</h1>
      <p className="text-lg text-muted-foreground mb-8">
        We couldn&apos;t find <code>{pathname}</code>.
      </p>

      {/* Lazarus Suggestions */}
      {!loading && suggestions.length > 0 && (
        <div className="w-full bg-card border border-border rounded-xl p-6 mb-8 text-left animate-in fade-in slide-in-from-bottom-4">
          <h3 className="text-sm font-semibold uppercase tracking-wider text-muted-foreground mb-4">
            Based on your URL, did you mean:
          </h3>
          <div className="flex flex-col gap-3">
            {suggestions.map((item) => (
              <Link 
                key={item.slug} 
                href={item.slug.startsWith('http') ? item.slug : `/${item.slug}`} // Ensure absolute path if not external
                className="group flex items-center justify-between p-3 rounded-lg hover:bg-muted/50 transition-colors"
              >
                <div>
                  <div className="font-medium group-hover:text-primary transition-colors">
                    {item.title}
                  </div>
                  <div className="text-xs text-muted-foreground line-clamp-1">
                    {item.description}
                  </div>
                </div>
                <ArrowRight className="w-4 h-4 opacity-0 group-hover:opacity-100 transition-opacity" />
              </Link>
            ))}
          </div>
        </div>
      )}

      <Button asChild size="lg">
        <Link href="/">Return to Home Base</Link>
      </Button>
    </div>
  )
}
